import { useField } from 'formik'
import { input, errorStyles } from '../../../../../helpers/theme'

const Input = ({ label, id, ...props }) => {
    const [field, { touched, error }] = useField(props)

    return (
        <div className='flex sm:items-center flex-col sm:flex-row gap-2'>
            <label htmlFor={ id } className='flex-[1_0_auto] cursor-pointer font-semibold'>
                { label }
            </label>
            <div className='w-full flex flex-col'>
                <input
                    id={ id }
                    { ...field }
                    { ...props }
                    className={
                        input.default +
                        input.size.md +
                        (touched && error && input.error)
                    }
                />
                { touched && error &&
                    <div className={ errorStyles.color }>{ error }</div>
                }
            </div>
        </div>
    )
}

export default Input
